* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  font-size: 5mm;
}

.search-bar{
  border: none;
}
.search {
  margin: 5px;
  padding-left: 10px;
  padding-bottom: 30px; 
}
.Appclearsky {
  background-image: url("https://motionarray.imgix.net/preview-17576Irtq7rFncd-low_0003.jpg?w=660&q=60&fit=max&auto=format");
  background-size: cover;
  background-position: bottom;
  transition: 0.5 ease-out;
}

.Appthunder {
  background-image: url("http://getwallpapers.com/wallpaper/full/e/1/1/1125961-cool-storm-clouds-wallpaper-1920x1080-hd.jpg");
  background-size: cover;
  background-position: bottom;
  transition: 0.5 ease-out;
}
.Appdrizzle {
  background-image: url("https://cdn.pixabay.com/photo/2015/08/03/22/25/rain-874041_640.jpg");
  background-size: cover;
  background-position: center;
  transition: 0.4 ease-in-out;
}
.Apphaze {
  background-image: url("https://get.wallhere.com/photo/sunlight-reflection-sky-sunrise-morning-mist-Sun-horizon-atmosphere-dusk-haze-light-cloud-fog-weather-dawn-atmospheric-phenomenon-atmosphere-of-earth-foggylake-694000.jpg");
  background-size: cover;
  background-position: center;
  transition: 0.4 ease-in-out;
}
.Appcloud {
  background-image: url("https://ak9.picdn.net/shutterstock/videos/28809439/thumb/1.jpg");
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease-out;
}

.Apprain {
  background-image: url("https://moneymarketing.co.za/wp-content/uploads/2019/12/Heavy-rain-1024x458.jpeg");
  background-size: cover;
  /*background-position: bottom;*/
  transition: 0.4 ease-out;
}

.Appsunny {
  background-image: url("https://bloximages.chicago2.vip.townnews.com/tucson.com/content/tncms/assets/v3/editorial/c/21/c2108eb0-5f3b-5918-b1e1-b57ac047394d/578e9192af03b.image.jpg?resize=1200%2C879");
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease-out;
}

.Appsnow {
  background-image: url("https://naturehdwallpapers.files.wordpress.com/2013/11/81960-1080p_snow_scenes_vol_2_no_13.jpg");
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease-out;
}
main{
  min-height: 100vh;
  padding: 25px;
}

.search .search-bar{
  display: block;
  width: 100%;
  padding: 15px ;
  appearance: none;
  background-color: rgba(225,225,225,0.8);
  margin-top: 10px;
  box-shadow: rgba(0,0,0,0.2);
  color: #313131;
  font-size: 20px;
  transition: 0.4s ease;
}

.search .search-bar:focus{
  background-color: rgba(225,225,225,0.2);
}

.Locationbar .location{
  color: #FFF;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50,50,50,0.2);
}

.Locationbar .date{
  padding-top: 10px;
  color: #FFF;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50,50,50,0.2);
}

.weatherbar {
  text-align: center;
}

.weatherbar .degrees{
  position : relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(225,225,225,0.2);
  border-radius: 16px;
  padding : 15px 25px;
  color : #FFF;
  font-size: 102px;
  font-weight: 900;
  text-shadow: 3px 3px rgba(50,50,50,0.2);
  text-align: center;
  box-shadow: 3px 3px rgba(0,0,0,0.2);
}

.weatherbar .weather {
  color: #FFF;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50,50,50,0.2);
}
